<script>
  import { fade, fly, slide } from "svelte/transition";
    import { db } from './firebase';
    import { collectionData } from 'rxfire/firestore';
    import { startWith } from 'rxjs/operators';
    import { createEventDispatcher } from 'svelte';

    const dispatch = createEventDispatcher();
    
    // function remove(event) {
    //     console.log(event.target.name);
    //     dispatch('remove', event.target.name);
    // }
    
    function removeItem(event) {
        event.preventDefault()
        console.log(event.target.name);
        console.log(event.detail);
        const id = event.target.name;
        db.collection('links1').doc(id).delete();
    }
    
    export let uid; 
    
    const query = db.collection('links1').where('uid', '==', uid).orderBy('created');
    const links1 = collectionData(query, 'id').pipe(startWith([]));
    // console.log(links1);

    // let selecionado = false;
    // $: selecionado = links1.filter(t => t.selected).length;
</script>

<style>
    .linksrapidos{
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    }
    .deletar{
        float: none;
        text-align: center;
        padding: 10px;
        height: 1.1em;
        width: 1.1em;
        line-height: 1.1em;
        font-size: 18px;
        right: 0;
        position: absolute;
        background-color: #00000030;
        z-index: 99999;
    } 
    .deletar:hover{
        background-color: #E26D5A;
    }
</style>

    <!-- {uid} -->
    <div class="linksrapidos">
        {#each $links1 as link}
            <label transition:slide>
                <!-- selecionar para editar -->
                <!-- <input class="uk-checkbox" type="checkbox" bind:checked={link.selected} /> -->
                <a href="#nav" class="deletar" name={link.id} on:click={removeItem}>x</a>
                <a href={link.link}>
                    {link.nome}
                <img src={link.imagem} alt="" />
                </a>
            </label>
        {/each}
    </div>