<script>
    import { createEventDispatcher } from 'svelte';
    import { fade, fly, slide, scale } from "svelte/transition";

    const dispatch = createEventDispatcher();
    
    function remove() {
		dispatch('remove', { id });
	}

	function toggleStatus() {
        let newStatus = !complete;
		dispatch('toggle', {
            id,
            newStatus
        });
    }
    
    export let id; // document ID
    export let text;
    export let complete;
</script>

<style>
    .is-complete {
        text-decoration: line-through;
        color: #8aff83;
    }
    li span{
        min-width: 60%;
        display: inline-block;
    }
</style>

<li transition:scale>
{#if complete}
    <span class="is-complete">{ text }</span>
    <button class="uk-icon-button uk-margin-small-right" uk-icon="check" on:click={toggleStatus}></button>
{:else}
    <span>{ text }</span>
    <button class="uk-icon-button uk-margin-small-right" uk-icon="close" on:click={toggleStatus}></button>
{/if}
    <button class="uk-icon-button uk-margin-small-right" uk-icon="trash" on:click={remove}></button>
</li>