  function addSite() {
    var ourPostData = {
      title: nomesite,
      content: urlsite,
      servidores: servselecionado,
      status: "publish",
    };

    var createPost = new XMLHttpRequest();
    createPost.open("POST", "http://" + URL + "/wp-json/wp/v2/sites");
    createPost.setRequestHeader("Authorization", "Bearer " + tokenteste3lados);
    // createPost.setRequestHeader("X-WP-Nonce", nonce.nonce);
    createPost.setRequestHeader(
      "Content-Type",
      "application/json; charset=UTF-8"
    );
    // createPost.setRequestHeader("Accept", "*/*");
    // createPost.setRequestHeader("Access-Control-Allow-Headers", "*");
    // createPost.setRequestHeader("Access-Control-Allow-Origin", "*");
    // createPost.setRequestHeader("Cache-Control", "no-cache");
    // createPost.setRequestHeader("Origin", "http://localhost:5000/");
    createPost.send(JSON.stringify(ourPostData));
    createPost.onreadystatechange = function() {
      console.log(createPost);
      if (createPost.readyState == 4) {
        if (createPost.status == 201) {
          nomesite = "";
          urlsite = "";
          window.location = "/";
        } else {
          alert("Error - try again.");
        }
      }
    };
  }

  function addServer() {
    var ourPostData = {
      name: servernome,
      nrancher: nrancher,
      nip: nip,
      npma: npma,
    };

    var createPost = new XMLHttpRequest();
    createPost.open("POST", "http://" + URL + "/wp-json/wp/v2/servidores");
    createPost.setRequestHeader("Authorization", "Bearer " + tokenteste3lados);
    createPost.setRequestHeader(
      "Content-Type",
      "application/json; charset=UTF-8"
    );
    createPost.send(JSON.stringify(ourPostData));
      console.log(ourPostData);
    createPost.onreadystatechange = function() {
      if (createPost.readyState == 4) {
        if (createPost.status == 201) {
          servernome = "";
          nrancher = "";
          nip = "";
          npma = "";
          window.location = "/";
        } else {
          alert("Error - try again.");
        }
      }
    };
  }

  let mostrar = false;
  const toggleMostrarForms = () => (mostrar = !mostrar);
</script>

<style>
  .forms{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    }
  .formsites {
    margin: 20px;
    display: flex;
    flex-direction: column;
  }
  .formsites > label{
    /* flex: 1; */
    display: block;
    margin: 10px 10px;
  }
  .listasites a:first-child {
    flex: 35;
  }
  .listasites a:last-child {
    flex: 1;
    color: #e26d5a;
  }
  .listasites a:last-child:hover {
    background-color: #ffc85720;
  }
  .listasites {
    display: flex;
  }
  .sites {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  }
  .servers {
    padding: 30px;
    border-bottom: 1px solid #eeeeee20;
    margin: 15px;
    background-color: #00000020;
    border-radius: 9px;
  }
  .detalhes-server h6 {
      font-size: 15px;
      line-height: 1.3em;
      margin: 0;
      color: #ffffff60;
      font-family: overpass;
      text-align: center;
  }
  .detalhes-server h6:first-child {
      font-size: 17px;
      letter-spacing: 1px;
      color: #ffffff90;
  }  .detalhes-server h6:hover {
      color: #fff;
  }
  .uk-accordion-title{
    font-size: 1.4em;
    font-family: 'Montserrat Subrayada', sans-serif;
  }
  li.uk-open > a {
      color: #ffde99 !important;
  }
</style>

<div class="main">
  <div class="sidebar esquerda">
  </div>

  <div class="centro">

    <div class="colunas nested">
      <h1>Sites
      <button uk-icon="icon: {mostrar ? 'minus-circle' : 'plus-circle'}" class="uk-button uk-button-body" on:click={toggleMostrarForms}></button>
      </h1>
      
      
      {#if mostrar}
      <div
        class="uk-card uk-card-primary uk-width-1-1@m forms"
        style="padding:30px;" transition:slide>
          <div class="formsites">
            <h2>Adicionar Site</h2>
                <label>Site
                    <input
                    class="uk-input"
                    type="text"
                    placeholder="Nome"
                    bind:value={nomesite} />
                </label>
                <label>Link
                    <input  
                    class="uk-input"
                    type="text"
                    placeholder="URL"
                    bind:value={urlsite} />
                </label>
                <label>Servidor
                    <select class="uk-select" bind:value={servselecionado}>
                        <option value="" />
                        {#each servidores as server}
                        <option value={server.id}>{server.name}</option>
                        {/each}
                    </select>
                </label>
            <button
                on:click={addSite}
                class="uk-button uk-button-default" style="margin: 10px">
                Adicionar Site
            </button>
            </div>

          <div class="formsites">
           <h2>Adicionar Servidor</h2>
                <label>Servidor
                    <input
                    required
                    class="uk-input"
                    type="text"
                    placeholder="Nome"
                    bind:value={servernome} />
                </label>
                <label>IP
                    <input  
                    required
                    class="uk-input"
                    type="text"
                    placeholder="ip"
                    bind:value={nip} />
                </label>
                <label>Rancher
                    <input  
                    required
                    class="uk-input"
                    type="text"
                    placeholder="URL"
                    bind:value={nrancher} />
                </label>
                <label>PMA
                    <input  
                    class="uk-input"
                    type="text"
                    placeholder="URL"
                    bind:value={npma} />
                </label>
                <button
                    on:click={addServer}
                    class="uk-button uk-button-default" style="margin: 10px">
                    Adicionar Servidor
                </button>
            </div>
         </div>
         {/if}

      <!-- id = itemsTab1.filter(t => t.selected)[0].id; -->
      <div class="sites">
        {#each servidores as server}
          <div class="servers">
            
            <ul uk-accordion="collapsible: true">
              <li>
                  <a class="uk-accordion-title" href="#">{server.name}</a>
                  <div class="uk-accordion-content">
                      <div class="detalhes-server">
                        <h6>{server.ip}</h6>
                        <h6><a href="{server.rancher}">{server.rancher}</a></h6>
                        <h6><a href="{server.phpmyadmin}">{server.phpmyadmin}</a></h6>
                    </div>
                  </div>
              </li>
             </ul>
             
            {#each sites3lados as site, i}
              {#if site.servidores == server.id}
                <div class="listasites" transition:slide>
                  <a href="#asd"> {site.title.rendered}</a>
                  <a href="#remove" name={site.id} on:click={removeSite}>x</a>
                </div>
              {/if}
            {/each}
          </div>
        {/each}
      </div>
    </div>
  </div>
</div>
